// Colors
$color-primary-first: #282c34
$color-primary-second: #343a47
$color-active: #d2dcf1

@mixin height-100vh
  height: 100vh

body
  color: $color-active
  background-color: $color-primary-first

.App
  //text-align: center
  @include height-100vh

  .main-left
    @include height-100vh
    position: absolute
    background-color: $color-primary-first
    clip-path: polygon(0% 0%, 80% 0%, 20% 100%, 0% 100%)

  .main-right
    @include height-100vh
    position: absolute
    background-color:  $color-primary-second
    clip-path: polygon(80% 0%, 100% 0%, 100% 100%, 20% 100%)

  .content
    // transform: skew(20deg)

